let isRedirecting = false;

const clearAuthStorage = () => {
  localStorage.removeItem('authToken');
  localStorage.removeItem('userData');
  localStorage.removeItem('adminToken');
  localStorage.removeItem('adminLoggedIn');
  localStorage.removeItem('adminInfo');
  localStorage.removeItem('shopAdminToken');
  localStorage.removeItem('shopAdminProvider');
};

export const handleAuthRedirect = (overridePath) => {
  if (isRedirecting) {
    return;
  }

  const hasUserToken = Boolean(localStorage.getItem('authToken'));
  const hasAdminToken = Boolean(localStorage.getItem('adminToken'));
  const hasShopAdminToken = Boolean(localStorage.getItem('shopAdminToken'));

  // If no tokens are present and no override path is provided, nothing to do
  if (!overridePath && !hasUserToken && !hasAdminToken && !hasShopAdminToken) {
    return;
  }

  isRedirecting = true;

  const targetPath = overridePath
    || (hasAdminToken
      ? '/admin/login'
      : hasShopAdminToken
        ? '/shop-admin/login'
        : '/login');

  clearAuthStorage();

  if (window.location.pathname !== targetPath) {
    window.location.replace(targetPath);
  } else {
    window.location.reload();
  }

  // Reset redirect flag after navigation attempt to allow future redirects in long-lived sessions
  setTimeout(() => {
    isRedirecting = false;
  }, 1500);
};

export const resetAuthRedirectState = () => {
  isRedirecting = false;
};

