import React, { useEffect, useMemo, useState } from 'react';
import { useNavigate, useParams, useSearchParams } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import { apiBaseUrl } from '../config';

const CheckoutPage = () => {
  const navigate = useNavigate();
  const { productId } = useParams();
  const [searchParams] = useSearchParams();
  const quantityParam = parseInt(searchParams.get('quantity')) || 1;
  const isBatchMode = searchParams.get('batch') === 'true';
  const { user, token } = useAuth();

  const [product, setProduct] = useState(null);
  const [loading, setLoading] = useState(true);
  const [checkingOut, setCheckingOut] = useState(false);
  const [batchItems, setBatchItems] = useState([]);
  const [shippingAddress, setShippingAddress] = useState({
    full_name: user?.name || '',
    phone: user?.phone || '',
    address_line1: '',
    address_line2: '',
    city: '',
    state: '',
    pincode: '',
    country: 'India'
  });

  useEffect(() => {
    if (!user) {
      navigate('/login');
      return;
    }

    const initialiseCart = () => {
      if (!isBatchMode) {
        setBatchItems([]);
        fetchProduct(productId);
        return;
      }

      try {
        const queue = JSON.parse(localStorage.getItem('shopCheckoutQueue') || '[]');
        if (!Array.isArray(queue) || queue.length === 0) {
          localStorage.removeItem('shopCheckoutQueue');
          navigate('/shop', { replace: true });
          return;
        }

        const storedCart = JSON.parse(localStorage.getItem('shopCart') || '[]');
        const itemsWithDetails = queue.map(entry => {
          const detail = storedCart.find(c => Number(c.id) === Number(entry.id));
          return {
            id: entry.id,
            quantity: entry.quantity,
            title: detail?.title || '',
            price: Number(detail?.price || 0),
            discountedPrice: detail?.discountedPrice != null ? Number(detail.discountedPrice) : null,
            originalPrice: Number(detail?.originalPrice || detail?.price || 0),
            image: detail?.image || null
          };
        });
        setBatchItems(itemsWithDetails);
        fetchProduct(productId);
      } catch (error) {
        console.error('Failed to load checkout queue:', error);
        localStorage.removeItem('shopCheckoutQueue');
        navigate('/shop', { replace: true });
      }
    };

    initialiseCart();
  }, [navigate, user, productId, isBatchMode]);

  const fetchProduct = async (id) => {
    try {
      setLoading(true);
      const response = await fetch(`${apiBaseUrl}/api/products?product_id=${id}`);
      if (response.ok) {
        const data = await response.json();
        if (data.products && data.products.length > 0) {
          setProduct(data.products[0]);
        } else {
          setProduct(null);
        }
      } else {
        setProduct(null);
      }
    } catch (error) {
      console.error('Error fetching product:', error);
      setProduct(null);
    } finally {
      setLoading(false);
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setShippingAddress(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const validateAddress = () => {
    if (!shippingAddress.full_name.trim()) {
      alert('Please enter full name');
      return false;
    }
    if (!shippingAddress.phone.trim()) {
      alert('Please enter phone number');
      return false;
    }
    if (!shippingAddress.address_line1.trim()) {
      alert('Please enter address');
      return false;
    }
    if (!shippingAddress.city.trim()) {
      alert('Please enter city');
      return false;
    }
    if (!shippingAddress.state.trim()) {
      alert('Please enter state');
      return false;
    }
    if (!shippingAddress.pincode.trim() || shippingAddress.pincode.length !== 6) {
      alert('Please enter valid 6-digit pincode');
      return false;
    }
    return true;
  };

  const effectiveCartItems = useMemo(() => {
    if (!isBatchMode) {
      if (!product) return [];
      const unitPrice = product.discounted_price || product.original_price || 0;
      return [{
        id: productId,
        title: product.title,
        quantity: quantityParam,
        unitPrice,
        originalPrice: product.original_price,
        image: product.images && product.images.length > 0
          ? (product.images[0].startsWith('http') ? product.images[0] : `${apiBaseUrl}${product.images[0]}`)
          : null
      }];
    }

    return batchItems.map(item => {
      const unitPrice = item.discountedPrice != null ? item.discountedPrice : item.price;
      return {
        id: item.id,
        title: item.title,
        quantity: item.quantity,
        unitPrice,
        originalPrice: item.originalPrice,
        image: item.image
      };
    });
  }, [isBatchMode, batchItems, product, productId, quantityParam]);

  const grandTotal = useMemo(() => {
    return effectiveCartItems.reduce((sum, item) => sum + item.unitPrice * item.quantity, 0);
  }, [effectiveCartItems]);

  const handleCheckout = async () => {
    if (!validateAddress()) return;

    try {
      setCheckingOut(true);

      const orderResponse = await fetch(`${apiBaseUrl}/api/shop/create-order`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({
          cart_items: effectiveCartItems.map(item => ({
            product_id: Number(item.id),
            quantity: item.quantity
          })),
          shipping_address: shippingAddress,
          total_amount: grandTotal
        })
      });

      if (!orderResponse.ok) {
        const error = await orderResponse.json();
        alert(error.error || 'Failed to create order');
        setCheckingOut(false);
        return;
      }

      const orderData = await orderResponse.json();

      const options = {
        key: orderData.razorpay_key_id,
        amount: orderData.amount,
        currency: orderData.currency,
        name: 'Unlimited Hub',
        description: 'Cart Checkout',
        order_id: orderData.order_id,
        handler: async function(response) {
          try {
            const verifyResponse = await fetch(`${apiBaseUrl}/api/shop/verify-payment`, {
              method: 'POST',
              headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${token}`
              },
              body: JSON.stringify({
                razorpay_order_id: response.razorpay_order_id,
                razorpay_payment_id: response.razorpay_payment_id,
                razorpay_signature: response.razorpay_signature,
                order_id: orderData.order_id
              })
            });

            if (verifyResponse.ok) {
              const verifyData = await verifyResponse.json();
              localStorage.removeItem('shopCheckoutQueue');
              localStorage.removeItem('shopCart');
              setCheckingOut(false);
              navigate(`/shop/order-confirmation/${verifyData.order_id}`);
            } else {
              const error = await verifyResponse.json();
              alert(error.error || 'Payment verification failed');
              setCheckingOut(false);
            }
          } catch (error) {
            console.error('Payment verification error:', error);
            alert('Payment verification failed. Please contact support.');
            setCheckingOut(false);
          }
        },
        prefill: {
          name: shippingAddress.full_name,
          email: user?.email || '',
          contact: shippingAddress.phone
        },
        theme: { color: '#667eea' },
        modal: {
          ondismiss: function() {
            setCheckingOut(false);
          }
        }
      };

      const razorpay = new window.Razorpay(options);
      razorpay.open();
      
    } catch (error) {
      console.error('Checkout error:', error);
      alert('Failed to process checkout. Please try again.');
      setCheckingOut(false);
    }
  };

  if (loading) {
    return (
      <div className="checkout-page page-with-bottom-nav">
        <div className="loading-container">
          <div className="loading-spinner"></div>
          <p>Loading...</p>
        </div>
      </div>
    );
  }

  if (!product && effectiveCartItems.length === 0) {
    return (
      <div className="checkout-page page-with-bottom-nav">
        <div className="error-container">
          <p>Cart items not found</p>
          <button className="btn-primary" onClick={() => navigate('/shop')}>
            Back to Shop
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="checkout-page page-with-bottom-nav">
      <div className="checkout-header">
        <button className="back-btn" onClick={() => navigate(-1)}>
          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
            <path d="M19 12H5M12 19l-7-7 7-7"/>
          </svg>
        </button>
        <h1>Checkout</h1>
        <div style={{ width: 24 }}></div>
      </div>

      <div className="checkout-content">
        <div className="checkout-summary">
          <h2>Order Summary</h2>
          <div className="checkout-product-list">
            {effectiveCartItems.map(item => (
              <div key={item.id} className="checkout-product-item">
                {item.image && (
                  <img
                    src={item.image}
                    alt={item.title}
                    className="checkout-product-image"
                  />
                )}
                <div className="checkout-product-info">
                  <h3>{item.title}</h3>
                  <p>Quantity: {item.quantity}</p>
                  <div className="checkout-product-price">
                    <span className="price-main">{item.unitPrice}</span>
                    {item.originalPrice && item.originalPrice !== item.unitPrice && (
                      <span className="price-strike">{item.originalPrice}</span>
                    )}
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>

        <div className="checkout-section">
          <h2>Shipping Address</h2>
          <div className="checkout-form">
            <input
              type="text"
              name="full_name"
              placeholder="Full Name *"
              value={shippingAddress.full_name}
              onChange={handleInputChange}
              required
            />
            <input
              type="tel"
              name="phone"
              placeholder="Phone Number *"
              value={shippingAddress.phone}
              onChange={handleInputChange}
              required
            />
            <input
              type="text"
              name="address_line1"
              placeholder="Address Line 1 *"
              value={shippingAddress.address_line1}
              onChange={handleInputChange}
              required
            />
            <input
              type="text"
              name="address_line2"
              placeholder="Address Line 2"
              value={shippingAddress.address_line2}
              onChange={handleInputChange}
            />
            <div className="checkout-form-row">
              <input
                type="text"
                name="city"
                placeholder="City *"
                value={shippingAddress.city}
                onChange={handleInputChange}
                required
              />
              <input
                type="text"
                name="state"
                placeholder="State *"
                value={shippingAddress.state}
                onChange={handleInputChange}
                required
              />
            </div>
            <input
              type="text"
              name="pincode"
              placeholder="Pincode *"
              value={shippingAddress.pincode}
              onChange={handleInputChange}
              maxLength="6"
              pattern="[0-9]{6}"
              required
            />
          </div>
        </div>

        <div className="checkout-total">
          <h2>Order Total</h2>
          <div className="checkout-total-row">
            <span>Subtotal:</span>
            <strong>{grandTotal.toLocaleString()}</strong>
          </div>
          <div className="checkout-total-row">
            <span>Shipping:</span>
            <strong>Free</strong>
          </div>
          <div className="checkout-total-row checkout-total-final">
            <span>Total:</span>
            <strong>{grandTotal.toLocaleString()}</strong>
          </div>
        </div>
      </div>

      <div className="checkout-actions">
        <button 
          className="btn-checkout" 
          onClick={handleCheckout}
          disabled={checkingOut}
        >
          {checkingOut ? 'Processing Payment...' : `Pay ${grandTotal.toLocaleString()}`}
        </button>
      </div>
    </div>
  );
};

export default CheckoutPage;

