import React, { useState, useEffect, useMemo } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import { apiBaseUrl } from '../config';
import BottomNavigation from './BottomNavigation';

const CART_STORAGE_KEY = 'shopCart';

const loadStoredCart = () => {
  try {
    const stored = JSON.parse(localStorage.getItem(CART_STORAGE_KEY) || '[]');
    return Array.isArray(stored) ? stored : [];
  } catch {
    return [];
  }
};

const ProductDetailPage = () => {
  const navigate = useNavigate();
  const { productId } = useParams();
  const { user } = useAuth();
  
  const [product, setProduct] = useState(null);
  const [selectedImage, setSelectedImage] = useState(0);
  const [quantity, setQuantity] = useState(1);
  const [loading, setLoading] = useState(true);
  const [addingToCart, setAddingToCart] = useState(false);
  const [cartItems, setCartItems] = useState(() => loadStoredCart());
  const [showCartDrawer, setShowCartDrawer] = useState(false);

  useEffect(() => {
    const fetchProductDetails = async () => {
      if (!productId) return;

    try {
      setLoading(true);
      const response = await fetch(`${apiBaseUrl}/api/products?product_id=${productId}`);
      if (response.ok) {
        const data = await response.json();
        if (data.products && data.products.length > 0) {
          setProduct(data.products[0]);
            setSelectedImage(0); // Reset image selection when product changes
          } else {
            setProduct(null);
          }
        } else {
          setProduct(null);
      }
    } catch (error) {
      console.error('Error fetching product:', error);
        setProduct(null);
    } finally {
      setLoading(false);
    }
  };

    fetchProductDetails();
  }, [productId]);

  useEffect(() => {
    setCartItems(loadStoredCart());
  }, []);

  useEffect(() => {
    document.body.style.overflow = showCartDrawer ? 'hidden' : 'unset';
    return () => {
      document.body.style.overflow = 'unset';
    };
  }, [showCartDrawer]);

  const persistCart = (items) => {
    setCartItems(items);
    localStorage.setItem(CART_STORAGE_KEY, JSON.stringify(items));
  };

  const handleDrawerQuantityChange = (productKey, delta) => {
    persistCart(
      cartItems.map(item => {
        if (item.id === productKey) {
          const nextQty = Math.max(1, item.quantity + delta);
          return { ...item, quantity: nextQty };
        }
        return item;
      })
    );
  };

  const handleDrawerRemove = (productKey) => {
    persistCart(cartItems.filter(item => item.id !== productKey));
  };

  const handleDrawerCheckout = (item) => {
    setShowCartDrawer(false);
    navigate(`/shop/checkout/${item.id}?quantity=${item.quantity}`);
  };

  const handleBuyAll = () => {
    if (cartItems.length === 0) {
      setShowCartDrawer(false);
      return;
    }

    const grouped = cartItems.reduce((acc, item) => {
      const existing = acc.find(entry => entry.id === item.id);
      if (existing) {
        existing.quantity += item.quantity;
      } else {
        acc.push({ id: item.id, quantity: item.quantity });
      }
      return acc;
    }, []);

    localStorage.setItem('shopCheckoutQueue', JSON.stringify(grouped));
    setShowCartDrawer(false);

    const first = grouped[0];
    navigate(`/shop/checkout/${first.id}?quantity=${first.quantity}&batch=true`);
  };

  const cartSubtotal = useMemo(() => {
    return cartItems.reduce((total, item) => {
      const linePrice = Number(item.price) || 0;
      return total + linePrice * item.quantity;
    }, 0);
  }, [cartItems]);

  const addToCart = () => {
    if (!user) {
      alert('Please login to add items to cart');
      navigate('/login');
      return;
    }

    setAddingToCart(true);
    try {
    const existingCart = loadStoredCart();
      const productKey = parseInt(productId, 10);
      const primaryImage = product.images && product.images.length > 0 ? product.images[0] : null;
      const resolvedImage = primaryImage
        ? (primaryImage.startsWith('http') ? primaryImage : `${apiBaseUrl}${primaryImage}`)
        : null;
      const priceToUse = product.discounted_price || product.original_price || 0;

      const cartItem = {
        id: productKey,
        title: product.title,
        price: Number(priceToUse),
        originalPrice: product.original_price || priceToUse,
        discountedPrice: product.discounted_price || null,
        image: resolvedImage,
        quantity: quantity,
        category: product.category_name || '',
        seller: product.provider_name || '',
        isAdminProduct: !!product.is_admin_product
      };

      const updatedCart = Array.isArray(existingCart) ? [...existingCart] : [];
      const existingIndex = updatedCart.findIndex(item => item.id === cartItem.id);

      if (existingIndex >= 0) {
        updatedCart[existingIndex].quantity += cartItem.quantity;
      } else {
        updatedCart.push(cartItem);
      }

      persistCart(updatedCart);
      setShowCartDrawer(true);
    } catch (error) {
      console.error('Error updating cart:', error);
      alert('Failed to add to cart. Please try again.');
    } finally {
      setAddingToCart(false);
    }
  };

  const buyNow = () => {
    if (!user) {
      alert('Please login to purchase');
      navigate('/login');
      return;
    }
    navigate(`/shop/checkout/${productId}?quantity=${quantity}`);
  };

  if (loading) {
    return (
      <div className="product-detail-page page-with-bottom-nav">
        <div className="loading-container">
          <div className="loading-spinner"></div>
          <p>Loading product...</p>
        </div>
      </div>
    );
  }

  if (!product) {
    return (
      <div className="product-detail-page page-with-bottom-nav">
        <div className="error-container">
          <p>Product not found</p>
          <button className="btn-primary" onClick={() => navigate('/shop')}>
            Back to Shop
          </button>
        </div>
      </div>
    );
  }

  const images = product.images || [];

  return (
    <div className="product-detail-page page-with-bottom-nav">
      {/* Header */}
      <div className="product-detail-header">
        <button className="back-btn" onClick={() => navigate(-1)}>
          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
            <path d="M19 12H5M12 19l-7-7 7-7"/>
          </svg>
        </button>
        <h1>Product Details</h1>
        <button className="icon-btn">
          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
            <path d="M20.84 4.61a5.5 5.5 0 0 0-7.78 0L12 5.67l-1.06-1.06a5.5 5.5 0 0 0-7.78 7.78l1.06 1.06L12 21.23l7.78-7.78 1.06-1.06a5.5 5.5 0 0 0 0-7.78z"/>
          </svg>
        </button>
      </div>

      {/* Product Content */}
      <div className="product-detail-content">
        {/* Image Gallery */}
        <div className="product-gallery">
          <div className="main-image">
            {images.length > 0 ? (
              <img 
                src={images[selectedImage]?.startsWith('http') ? images[selectedImage] : `${apiBaseUrl}${images[selectedImage]}`} 
                alt={product.title}
                onError={(e) => {
                  e.target.style.display = 'none';
                  if (e.target.nextSibling) {
                    e.target.nextSibling.style.display = 'flex';
                  }
                }}
              />
            ) : null}
            <div className="no-image-large" style={{ display: images.length > 0 ? 'none' : 'flex' }}>
                <span></span>
                <p>No Image</p>
              </div>
          </div>
          
          {images.length > 1 && (
            <div className="image-thumbnails">
              {images.map((img, idx) => (
                <div
                  key={idx}
                  className={`thumbnail ${selectedImage === idx ? 'active' : ''}`}
                  onClick={() => setSelectedImage(idx)}
                >
                  <img 
                    src={img.startsWith('http') ? img : `${apiBaseUrl}${img}`} 
                    alt={`${product.title} ${idx + 1}`}
                    onError={(e) => {
                      e.target.style.display = 'none';
                    }}
                  />
                </div>
              ))}
            </div>
          )}
        </div>

        {/* Product Info */}
        <div className="product-info-detail">
          <div className="product-category-badge">
            {product.category_name}
            {product.subcategory_name && ` / ${product.subcategory_name}`}
          </div>
          
          <h2 className="product-title-detail">{product.title}</h2>
          
          <div className="product-price-detail">
            {product.discounted_price ? (
              <>
                <span className="price-main">{product.discounted_price}</span>
                <span className="price-strike">{product.original_price}</span>
                <span className="price-discount">
                  {Math.round(((product.original_price - product.discounted_price) / product.original_price) * 100)}% OFF
                </span>
              </>
            ) : (
              <span className="price-main">{product.original_price}</span>
            )}
          </div>

          {/* Stock Info */}
          <div className="stock-info">
            {product.stock_quantity > 0 ? (
              <span className="in-stock"> In Stock ({product.stock_quantity} available)</span>
            ) : (
              <span className="out-of-stock"> Out of Stock</span>
            )}
          </div>

          {/* Description */}
          <div className="product-description-section">
            <h3>Product Description</h3>
            <p>{product.description}</p>
          </div>

          {/* Provider Info */}
          {product.provider_name && (
            <div className="provider-info">
              <span>Sold by: <strong>{product.provider_name}</strong></span>
            </div>
          )}

          {/* Quantity Selector */}
          {product.stock_quantity > 0 && (
            <div className="quantity-selector">
              <label>Quantity:</label>
              <div className="quantity-controls">
                <button 
                  className="qty-btn" 
                  onClick={() => setQuantity(Math.max(1, quantity - 1))}
                  disabled={quantity <= 1}
                >
                  -
                </button>
                <span className="qty-display">{quantity}</span>
                <button 
                  className="qty-btn" 
                  onClick={() => setQuantity(Math.min(product.stock_quantity, quantity + 1))}
                  disabled={quantity >= product.stock_quantity}
                >
                  +
                </button>
              </div>
            </div>
          )}
        </div>
      </div>

      {/* Fixed Bottom Actions */}
      {product.stock_quantity > 0 && (
        <div className="product-actions-fixed">
          <button
            className="btn-add-cart"
            onClick={addToCart}
            disabled={addingToCart}
          >
            {addingToCart ? 'Adding...' : 'Add to Cart'}
          </button>
          <button
            className="btn-buy-now"
            onClick={buyNow}
          >
            Buy Now
          </button>
        </div>
      )}

      {/* Cart Drawer */}
      <div
        className={`cart-drawer-overlay ${showCartDrawer ? 'open' : ''}`}
        onClick={() => setShowCartDrawer(false)}
      ></div>
      <div className={`cart-drawer ${showCartDrawer ? 'open' : ''}`}>
        <div className="cart-drawer-header">
          <h2>Added to Cart</h2>
          <button className="icon-btn" onClick={() => setShowCartDrawer(false)}>
            
          </button>
        </div>
        <div className="cart-drawer-body">
          {cartItems.length === 0 ? (
            <div className="empty-cart">
              <div className="empty-icon"></div>
              <p>Your cart is empty.</p>
            </div>
          ) : (
            <ul className="cart-drawer-list">
              {cartItems.map(item => {
                const lineTotal = (Number(item.price) || 0) * item.quantity;
                return (
                  <li key={item.id} className="cart-drawer-item">
                    <div className="cart-item-thumb">
                      {item.image ? (
                        <img src={item.image} alt={item.title} />
                      ) : (
                        <div className="placeholder-thumb"></div>
                      )}
                    </div>
                    <div className="cart-item-content">
                      <h3>{item.title}</h3>
                      <div className="cart-item-price">
                        {Number(item.price || 0).toLocaleString()}
                      </div>
                      <div className="cart-item-controls">
                        <div className="quantity-controls">
                          <button onClick={() => handleDrawerQuantityChange(item.id, -1)}>-</button>
                          <span>{item.quantity}</span>
                          <button onClick={() => handleDrawerQuantityChange(item.id, 1)}>+</button>
                        </div>
                        <button className="remove-btn" onClick={() => handleDrawerRemove(item.id)}>
                          Remove
                        </button>
                      </div>
                    </div>
                    <div className="cart-item-total">
                      <span>{lineTotal.toLocaleString()}</span>
                      <button className="primary-btn" onClick={() => handleDrawerCheckout(item)}>
                        Buy Now
                      </button>
                    </div>
                  </li>
                );
              })}
            </ul>
          )}
        </div>
        <div className="cart-drawer-footer">
          <div className="cart-subtotal">
            <span>Subtotal</span>
            <strong>{cartSubtotal.toLocaleString()}</strong>
          </div>
          <div className="cart-drawer-actions">
            <button className="secondary-btn" onClick={() => setShowCartDrawer(false)}>
              Continue Shopping
            </button>
            <button className="primary-btn" onClick={handleBuyAll}>
              Buy All
            </button>
          </div>
        </div>
      </div>

      <BottomNavigation />
    </div>
  );
};

export default ProductDetailPage;

