import React from 'react';
import ReactDOM from 'react-dom/client';
import axios from 'axios';
import './index.css';
import App from './App';
import { handleAuthRedirect } from './utils/authRedirect';

axios.interceptors.response.use(
  (response) => response,
  (error) => {
    const status = error?.response?.status;
    const config = error?.config || {};

    if (status === 401 && !config.skipAuthRedirect) {
      handleAuthRedirect(config.authRedirectPath);
    }

    return Promise.reject(error);
  }
);

const originalFetch = window.fetch;
window.fetch = async (...args) => {
  const response = await originalFetch(...args);

  let requestConfig;

  if (args[0] instanceof Request) {
    requestConfig = args[0];
  } else if (typeof args[1] === 'object' && args[1] !== null) {
    requestConfig = args[1];
  }

  const skipAuthRedirect = requestConfig?.skipAuthRedirect;
  const authRedirectPath = requestConfig?.authRedirectPath;

  if (response.status === 401 && !skipAuthRedirect) {
    handleAuthRedirect(authRedirectPath);
  }

  return response;
};

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);